
body {
  margin: 0;
  padding: 0;
  border: 0;
}
body .grid-box {
    width: 100vw;
    height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: fixed;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    top: 0;
    z-index: 1000;
}
body .grid-box .grid-container {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      height: 100vh;
      width: 940px;
      margin: 0 auto;
      padding: 0 10px;
      grid-gap: 20px;
      position: fixed;
}
body .grid-box .grid-container .grid-item {
        border: 1px dashed rgba(0, 0, 0, 0.5);
}
@media (max-width: 768px) {
body body .grid-box .grid-container {
      width: 764px;
      margin: 0 auto;
      padding: 0 2px;
      grid-gap: 4px;
}
}
@media (max-width: 320px) {
body body .grid-box .grid-container {
      width: 315px;
      margin: 0 auto;
      padding: 0 2.5px;
      grid-gap: 5px;
}
}
